// @flow

import { ImagePosition } from './image_atlas';
import assert from 'assert';

import type { StyleImage } from '../style/style_image';
import type Context from '../gl/context';
import type { Bin } from 'potpack';
import type { Callback } from '../types/callback';
import ImageManager from './image_manager';

class ImageManagerFactory {
    imageManagers: { [string]: ImageManager }

    constructor() {
        this.imageManagers = { "image_manager_default": new ImageManager() };
        this.imageManagers["image_manager_default"].setLoaded(true);
        //this.imageManagers = {};
    }

    getImage(id: string, sourceId = "image_manager_default"): ?StyleImage {
        return this._getImageManager(sourceId).getImage(id);
    }
    setEventedParent(parent: ?Evented, data?: Object | () => Object,sourceId = "image_manager_default"){
        this._getImageManager(sourceId).setEventedParent(parent,data);
    }
    addImage(id: string, image: StyleImage, sourceId = "image_manager_default") {
        if (!this.imageManagers[sourceId]) {
            this.imageManagers[sourceId] = new ImageManager();
        }
        this._getImageManager(sourceId).addImage(id, image);
    }
    updateImage(id: string, image: StyleImage, sourceId = "image_manager_default") {
        this._getImageManager(sourceId).updateImage(id,image);
    }
    removeImage(id: string, sourceId = "image_manager_default") {
        this._getImageManager(sourceId).removeImage(id);
    }

    listImages(sourceId = "image_manager_default"): Array<string> {
        let images = [];
        for (const key in this.imageManagers) {
            const imageManager = this.imageManagers[key];
            images.push(...imageManager.listImages())
        }
        return images;
    }

    getImages(ids: Array<string>, callback: Callback<{ [string]: StyleImage }>, sourceId = "image_manager_default") {
        this._getImageManager(sourceId).getImages(ids, callback)
    }

    getPixelSize(sourceId = "image_manager_default") {
        return this._getImageManager(sourceId).getPixelSize();
    }

    getPattern(id: string, sourceId = "image_manager_default"): ?ImagePosition {
        return this._getImageManager(sourceId).getPattern(id);
    }

    bind(context: Context, sourceId = "image_manager_default") {
        this._getImageManager(sourceId).bind(context);
    }
    isLoaded(sourceId = "image_manager_default") {
        return this._getImageManager(sourceId).isLoaded();
    }
    isAllLoaded() {
        for (const key in this.imageManagers) {
            const imageManager = this.imageManagers[key];
            if (!imageManager.isLoaded()) {
                return false;
            }
        }
        return true;
    }
    beginFrame(sourceId = "image_manager_default") {
        return this._getImageManager(sourceId).beginFrame()
    }

    dispatchRenderCallbacks(ids: Array<string>,sourceId = "image_manager_default") {
        return this._getImageManager(sourceId).dispatchRenderCallbacks(ids)
    }
    setLoaded(loaded: boolean, sourceId = "image_manager_default") {
        return this._getImageManager(sourceId).setLoaded(loaded);
    }
    _getImageManager(sourceId) {
        if (this.imageManagers[sourceId]) {
            return this.imageManagers[sourceId];
        }
        return this.imageManagers["image_manager_default"];
    }


}

export default ImageManagerFactory;
