// @flow

class CRS {
    epsgCode;
    extent;
    constructor(epsgCode, extent, unit) {
        this.epsgCode = epsgCode;
        this.extent = extent;
        this.unit = unit || 'degree';
        CRS.set(this);
    }
    getExtent() {
        if (!this._rectifyExtent) {
            const width = this.extent[2] - this.extent[0];
            const height = this.extent[3] - this.extent[1];
            if (width === height) {
                this._rectifyExtent = [this.extent[0], this.extent[1], this.extent[2], this.extent[3]];
            } else {
                var a = Math.max(width, height);
                this._rectifyExtent = [this.extent[0], this.extent[3] - a, this.extent[0] + a, this.extent[3]]
            }
        }
        return this._rectifyExtent;
    }
    getOrigin() {
        return [this.extent[0], this.extent[3]];
    }
    getEpsgCode() {
        return this.epsgCode;
    }
    getUnit() {
        return this.unit;
    }
}
CRS.get = function (codeSpec) {
    for (const key in CRS) {
        if (CRS.hasOwnProperty(key)) {
            if (CRS[key].getEpsgCode && CRS[key].getEpsgCode() === codeSpec) {
                return CRS[key];
            }
        }
    }
    return null;
};
CRS.set = function (crs) {
    const key = crs.getEpsgCode().replace(":", "").toUpperCase();
    CRS[key] = crs;

};
CRS.EPSG4326 = new CRS('EPSG:4326', [-180, -90, 180, 90],"degree");
CRS.EPSG4490 = new CRS('EPSG:4490', [-180, -90, 180, 90],"degree");
CRS.EPSG4214 = new CRS('EPSG:4214', [-180, -90, 180, 90],"degree");
CRS.EPSG4610 = new CRS('EPSG:4610', [-180, -90, 180, 90],"degree");//new CRS('EPSG:4490', [73.62, 16.7, 134.77, 53.56]);
CRS.EPSG3857 = new CRS('EPSG:3857', [-20037508.3427892, -20037508.3427892, 20037508.3427892, 20037508.3427892],"meter");
export default CRS;
