
import getType from '../util/get_type';
import ValidationError from '../error/validation_error';

export default function validateStringObject(options) {
    const value = options.value;
    const key = options.key;
    const type = getType(value);

    if (type == 'string') {
        return [];
    }
    if (type == 'object') {
        const errors = [];
        for (const key in value) {
            const t = getType(value[key]);
            if (t !== 'string') {
                errors.push(new ValidationError(key, value[key], `string expected, ${t} found`));

            }
        }
        return errors;
    }
    return [new ValidationError(key, value, `string expected, ${type} found`)];
}
