// @flow

import GlyphManager from './glyph_manager';

class GlyphManagerFactory {
    glyphManagers: { [string]: GlyphManager }

    constructor(requestManager: RequestManager, localIdeographFontFamily: ?string) {
        this.requestManager = requestManager;
        this.localIdeographFontFamily = localIdeographFontFamily;
        this.glyphManagers = {"image_manager_default":new GlyphManager(this.requestManager, this.localIdeographFontFamily)};
    }
    _getGlyphManager(sourceId) {
        if (this.glyphManagers[sourceId]) {
            return this.glyphManagers[sourceId];
        }
        return this.glyphManagers["image_manager_default"];
    }
    setURL(url: ?string, sourceId = "image_manager_default") {
        if (!this.glyphManagers[sourceId]) {
            this.glyphManagers[sourceId] = new GlyphManager(this.requestManager, this.localIdeographFontFamily);
        }
        this._getGlyphManager(sourceId).setURL(url);
    }

    getGlyphs(glyphs: { [stack: string]: Array<number> }, callback: Callback<{ [stack: string]: { [id: number]: ?StyleGlyph } }>, sourceId = "image_manager_default") {
        this._getGlyphManager(sourceId).getGlyphs(glyphs, callback);
    }
}
export default GlyphManagerFactory;
